import React from "react";
import { Routes } from "react-router-dom";
import AuthorizedRoute from "./AuthorizedRoute";

const General = React.lazy(() => import("../settings/General"));
const Tags = React.lazy(() => import("../tags/tags"));
const Environments = React.lazy(() => import("../settings/environments"));
const Configurations = React.lazy(() => import("../settings/Configurations"));
const Licenses = React.lazy(() => import("../settings/Licenses"));
const LoginPageSettings = React.lazy(() => import("../settings/LoginPageSettings"));
const Git = React.lazy(() => import("../git/Git"));

export default function SettingsRoute() {
  return (
    <Routes>
      <AuthorizedRoute path="/" element={<General />} />
      <AuthorizedRoute path="tags" element={<Tags />} />
      <AuthorizedRoute path="environments" element={<Environments />} />
      <AuthorizedRoute path="configurations" element={<Configurations />} />
      <AuthorizedRoute path="license" element={<Licenses />} />
      <AuthorizedRoute path="loginPage" element={<LoginPageSettings />} />
      <AuthorizedRoute path="git" element={<Git />} />
    </Routes>
  );
}
